<!--Download:Ke 下载预览页 2023/01/24 18:31:53 YuAng-->

<template>
  <div class="ke-download">
    <ul>
      <li v-for="(item, index) in paths" :key="'li' + index" :class="{active: url.indexOf(item) >= 0}" @click="jumpNext(item)">
        {{index + 1}}、{{item}}
      </li>
    </ul>
    <iframe id="iframe" :src="url"  frameborder="0"></iframe>
  </div>
</template>
<script>
export default {
  name: 'Download',
  data () {
    return {
      host: 'https://www.21cnjy.com',
      paths: [],
      url: ''
    }
  },
  created () {
    this.paths = this.$route.query.paths.split(',')
    if (this.paths && this.paths.length > 0) {
      this.jumpNext(this.paths[0])
    }
  },
  methods: {
    jumpNext: function (path) {
      this.url = this.host + path
    }
  }
}
</script>

<style scoped>
.ke-download {
  padding: 20px;
  width: 100%;
  height: 100%;
}

ul {
  width: 250px;
  margin-right: 10px;
  color: #666;
  height: 100%;
  overflow: auto;
  float: left;
}

li {
  width: 100%;
  padding: 6px 0;
  cursor: pointer;
}

li.active {
  color: dodgerblue;
}

#iframe {
  height: 100%;
  width: calc(100% - 270px);
  float: left;
}
</style>
