<script>
import AlloyFinger from 'alloyfinger'
export default {
  data () {
    return {
      posX: 0,
      posY: 0,
      dis: 1,
      dis1: 1,
      angle: 0,
      style: {
        width: 375,
        height: 700
      },
      style1: {
        width: 375,
        height: 700
      },
      stop: false
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    getData () {
      const that = this
      const pic = this.$refs.pic
      this.af = new AlloyFinger(pic, {
        // rotate: function (evt) {
        //   console.log('实现旋转')
        //   that.angle += evt.angle
        // },
        pinch: function (evt) {
          console.log('实现缩放')
          this.stop = false
          that.dis = that.dis1 * evt.zoom
          that.style.width = that.style1.width * evt.zoom
          that.style.height = that.style1.height * evt.zoom
        },
        touchEnd: function () {
          this.stop = true
          const { width, height } = that.style1
          that.style1.width = that.style.width
          that.style1.height = that.style.height
          that.dis1 = that.dis
          this.$nextTick(() => {
            this.$refs.pic.scrollLeft = (that.style.width - width)
            this.$refs.pic.scrollTop = (that.style.height - height)
          })
        }
        // pressMove: function (evt) {
        //   console.log('实现移动')
        //   that.posX += evt.deltaX
        //   that.posY += evt.deltaY
        // },
        // tap: function (evt) {
        //   console.log('单击')
        //   // 点按触发
        // },
        // doubleTap: function (e) {
        //   console.log('双击')
