        // longTap: function (e) {
        //   console.log('长按')
        //   // 长按屏幕750ms触发
        // },
        // swipe: function (e) {
        //   // e.direction代表滑动的方向
        //   console.log('swipe' + e.direction)
        // }
      })
    }
  }
}
